import { PlusOutlined } from '@ant-design/icons';
import { Button, Popconfirm, Switch } from 'antd';
import { ColumnType } from 'antd/es/table';
import IconInfo from 'assets/Icon/IconInfo';
import IconPencil from 'assets/Icon/IconPencil';
import IconTrash from 'assets/Icon/IconTrash';
import Routers from 'constants/routers';
import { IJobForm, IJobList } from 'models/jobs';
import { useNavigate } from 'react-router-dom';

import { createJob, deleteJob, getJobById, getListJob, updateStatusJob } from 'apis/modules/Jobs';
import BaseTable from 'components/BaseTable';
import TableHead from 'components/BaseTable/TableHead';
import dayjs from 'dayjs';
import noti from 'libs/notification';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import IconCopy from 'assets/Icon/IconCopy';

const JobsPage: React.FC = () => {
  const { t } = useTranslation();
  const navi = useNavigate();
  const [dataSource, setDataSource] = useState<IJobList[]>([]);
  const [loading, setLoading] = useState(false);
  const [search, setSearch] = useState('');
  const [isProcess, setIsProcess] = useState(false);
  const [paging, setPaging] = useState({
    total: 0,
    limit: 10,
    page: 1
  });

  const columns: ColumnType<IJobList>[] = [
    {
      title: t('common.label.jobName'),
      dataIndex: 'jobName',
      key: 'jobName',
      fixed: true,
      width: '10%'
    },
    {
      title: t('common.label.skills'),
      dataIndex: 'skill',
      key: 'skill',
      render(value, record, index) {
        return (
          <span>
            {record.skills
              ?.map((item: any) => item.skillName)
              .concat()
              .join(', ')}
          </span>
        );
      },
      width: '20%'
    },
    {
      title: t('common.label.startDate'),
      dataIndex: 'startDate',
      key: 'startDate',
      width: '150px',
      render(value, record, index) {
        return <span>{dayjs(record.startDate).format('YYYY/MM/DD')}</span>;
      }
    },
    {
      title: t('common.label.endDate'),
      dataIndex: 'endDate',
      key: 'endDate',
      width: '150px',
      render(value, record, index) {
        return <span>{dayjs(record.endDate).format('YYYY/MM/DD')}</span>;
      }
    },
    {
      title: t('common.label.status'),
      dataIndex: 'status',
      key: 'status',
      width: '120px',
      render(value, record, index) {
        return (
          <Switch
            defaultChecked={record.status === 'ACTIVE' ? true : false}
            checkedChildren="On"
            unCheckedChildren="Off"
            disabled={isProcess}
            onChange={(value) => onSwitchChange(record.id as any, value)}
          />
        );
      }
    },
    {
      title: t('common.label.description'),
      dataIndex: 'description',
      key: 'description',
      render(value, record, index) {
        return <div className="truncate">{record.description}</div>;
      }
    },
    {
      title: t('common.label.action'),
      className: '!text-center !pl-8',
      key: 'action',
      width: '150px',
      render: (_, record) => (
        <div className="h-full flex items-center justify-center table--col-action">
          <Button className="outline-none border-0" onClick={() => navi(`${Routers['DetailJob']}/${record.id}`)}>
            <IconPencil />
          </Button>
          <Button
            className="outline-none border-0"
            onClick={() => handleCopyJob(record.id as any)}
            disabled={isProcess}
          >
            <IconCopy />
          </Button>
          <Button className="outline-none border-0">
            <IconInfo />
          </Button>
          <Popconfirm
            title="Are you sure delete this job?"
            onConfirm={() => onConfirmDelete(record.id as any)}
            okText="Yes"
            cancelText="No"
            okButtonProps={{ danger: true }}
          >
            <Button className="outline-none border-0">
              <IconTrash />
            </Button>
          </Popconfirm>
        </div>
      )
    }
  ];

  async function handleCopyJob(id: string | number) {
    try {
      setIsProcess(true);
      if (id) {
        const response: any = await getJobById(id);
        if (response && response.code === 'SUCCESS' && response.data) {
          const { data } = response;
          const payload = {
            jobName: `${data.jobName} Copy`,
            workingTime: data.workingTime,
            city: data.city,
            experience: data.experience,
            startDate: dayjs(data.startDate).format('YYYY-MM-DD'),
            endDate: dayjs(data.endDate).format('YYYY-MM-DD'),
            budget: data.budget && isNaN(data.budget) ? 0 : Number(data.budget),
            requirement: data.requirement,
            description: data.description,
            department: data.department,
            skills: data.skills.map((item: any) => item.skillName)
          };

          const rsCreate = (await createJob(payload)) as any;
          if (rsCreate && rsCreate.code === 'SUCCESS') {
            noti('success', 'Copy job successfully');
            fetchData({
              page: 1,
              condition: search || undefined
            });
          } else {
            noti('error', rsCreate.message);
          }
        } else {
          noti('error', response.message);
        }
      }
    } catch (error) {
      noti('error', 'Failed to copy data');
    } finally {
      setIsProcess(false);
    }
  }

  async function onSwitchChange(id: string | number, value: boolean) {
    const params = { id: id, status: value ? 'ACTIVE' : 'INACTIVE' };
    try {
      setIsProcess(true);
      const response: any = await updateStatusJob(params.id as any, params.status);
      if (response) {
        noti('success', 'Update status success');
        fetchData({
          page: paging.page,
          condition: search || undefined
        });
      } else {
        noti('error', 'Failed to update status');
      }
    } catch (error) {
      noti('error', 'Failed to update status');
    } finally {
      setIsProcess(false);
    }
  }

  const fetchData = async (params?: any) => {
    try {
      setLoading(true);
      const response: any = await getListJob({
        limit: paging.limit,
        page: paging.page,
        ...params
      });
      if (response && response.data) {
        setDataSource(response.data);
        setPaging((prev) => ({ ...prev, total: response.meta.total, page: response.meta.page }));
      }
    } catch (error) {
      noti('error', 'Failed to get data jobs');
      setDataSource([]);
    } finally {
      setLoading(false);
    }
  };

  const onSearch = (value: string) => {
    setSearch(value);
    setPaging((prev) => ({ ...prev, page: 1 }));
    if (value === '') {
      fetchData({ page: 1 });
      return;
    }
    const params = { condition: value, page: 1 };
    fetchData(params);
  };

  const onConfirmDelete = async (id: string | number) => {
    try {
      const response: any = await deleteJob(id);
      if (response && response.code === 'SUCCESS') {
        noti('success', 'Delete successfully');
        fetchData({
          page: 1,
          condition: search || undefined
        });
      } else {
        noti('error', response.message);
      }
    } catch (error: any) {
      noti('error', 'Failed to delete job');
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <div className="h-full">
      <div className="h-16 bg-white w-full">
        <div className="flex h-full flex-row justify-between items-center px-6">
          <div className="text-xl font-bold">{t('page.jobs.title')}</div>
          <Button
            type="primary"
            danger
            className="flex gap-2 items-center !bg-red rounded-sm"
            onClick={() => navi(Routers['CreateJob'])}
          >
            <PlusOutlined /> {t('page.jobs.label.addNew')}
          </Button>
        </div>
      </div>

      <BaseTable
        tClassName="m-6 mb-12 p-4"
        loading={loading}
        rowKey="key"
        columns={columns}
        dataSource={dataSource}
        currentPage={paging.page}
        showPaging
        pagination={{
          total: paging.total,
          pageSize: paging.limit,

          onChange: (newPage: number) => {
            setPaging((prevPaging) => ({ ...prevPaging, page: newPage }));
            fetchData({ page: newPage, condition: search || undefined });
          }
        }}
        searchComponent={<TableHead headTitle={t('page.jobs.label.listJob')} onSearch={onSearch}></TableHead>}
      />
    </div>
  );
};

export default JobsPage;
